@use 'base/variables' as v;
@use 'base/mixins' as m;

.side-header {
  @include m.bgImage('../../../build/img/header.jpg', v.$secundarioOpacity);
  padding: 5rem 0;
  
  .logo {
    max-width: 20rem;
    margin: 0 auto;
    @include m.tablet { 
      margin: 0;
    }
  }
  .header-content {
    padding: 10rem 0;
    @include m.tablet { 
      padding: 15rem 0;
    }

    h1 {
      color: v.$blanco;
      font-size: 4.5rem;
      
      @include m.tablet { 
        font-size: 6rem;
        max-width: 50%;
      }
      @include m.desktop { 
        max-width: 40%;
        
      }
    }
  }
}