@use 'variables' as v; 
@use 'mixins' as m; 

html {
  font-size: 62.5%;
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: inherit;
}
  
body {
  font-family: v.$fuente_principal;
  font-size: 1.6rem;
}
p {
  color: v.$negro;
  font-size: 2rem;
  line-height: 2;
}
.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 2rem  0 ;
}
h1 {
  font-size: 4rem;
}
h2 {  
}
h3 {
  font-size: 2.8rem;
  text-align: center;
  text-transform: uppercase;
}
ul {
  padding: 0;
  list-style: none;
}

.section {
  padding: 3rem 0;
  @include m.tablet { 
    padding: 8rem 0;
  }
}